#social{
	text-align: center;
	display: inline;
}

.sociali{
	list-style: none;
}

/* newly added */
#social li {
	float: right;
	padding: 10px;
}

.sociali a{
	width: 80px;
	height: 80px;
	background: #1f3d7a; /*color inside circle*/
	text-align: center;
	line-height: 80px;
	font-size: 35px;
	margin: 0 -10px;
	display: block;
	border-radius: 50%;
	position: relative;
	overflow: hidden;
	border: 3px solid #17202A; /*color of border*/
	z-index: 1;
}

.sociali a .fa{
	position: relative;
	color: #FFF; /*color of icons*/
	-webkit-transition: .5s;
	-o-transition: .5s;
	transition: .5s;
	z-index: 3;
}

.sociali a:hover .fa{
	color: #fff;
	-webkit-transform: rotateY(360deg);
	        transform: rotateY(360deg);
}

.sociali a:before{
	content: '';
	position: absolute;
	top: 100%;
	left: 0;
	width: 100%;
	height: 100%;
	background: #f00;
	-webkit-transition: .5s;
	-o-transition: .5s;
	transition: .5s;
	z-index: 2;
}

.sociali a:hover:before{
	top: 0;
}

.sociali:nth-child(1) a:before{
	background: #0077B5;
}

.sociali:nth-child(2) a:before{
	background: #f57d00;
}

.sociali:nth-child(3) a:before{
	background: #262626;
}

.sociali:nth-child(4) a:before{
	background: #00c300;
}


